import Link from 'next/link';

const navigationItems = [
  'Home',
  'About Us',
  'Learn and Empower',
  'Community',
  'Blog & Media',
  'Shop',
  'Contact'
];

const Logo = () => (
  <Link href="/" className="group flex items-center space-x-3">
    <div className="w-12 h-12 bg-white/10 rounded-full flex items-center justify-center 
      group-hover:bg-white/20 transition-all duration-300">
      <span className="text-2xl">⚔️</span>
    </div>
    <span className="text-2xl font-bold bg-gradient-to-r from-gray-200 to-white 
      text-transparent bg-clip-text group-hover:from-white group-hover:to-gray-300 
      transition-all duration-300">
      Shakti Sabha
    </span>
  </Link>
);

const NavLink = ({ item }: { item: string }) => (
  <Link
    href={`/${item === 'Home' ? '' : item.toLowerCase().replaceAll(' ', '-')}`}
    className="relative px-4 py-2 text-gray-100 font-medium text-sm hover:text-gray-300 
      transition-all duration-300 group"
  >
    {item}
    <span className="absolute bottom-0 left-0 w-full h-0.5 bg-white/70 transform 
      scale-x-0 group-hover:scale-x-100 transition-transform duration-300" />
  </Link>
);

const MobileMenu = () => {
  return (
    <div className="md:hidden">
      <details className="group relative">
        <summary className="list-none p-2 rounded-lg hover:bg-white/10 
          transition-all duration-300 cursor-pointer">
          <svg
            className="w-6 h-6 text-gray-300 hover:text-white transition-colors duration-300"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M4 6h16M4 12h16M4 18h16"
            />
          </svg>
        </summary>
        <div className="absolute right-0 w-56 mt-2 origin-top-right">
          <div className="bg-gray-900/95 backdrop-blur-md rounded-lg shadow-lg 
            border border-white/10 overflow-hidden">
            {navigationItems.map(item => (
              <Link
                key={item}
                href={`/${item === 'Home' ? '' : item.toLowerCase().replaceAll(' ', '-')}`}
                className="block px-4 py-3 text-sm text-gray-300 hover:bg-white/10 
                  hover:text-white transition-all duration-300 first:rounded-t-lg 
                  last:rounded-b-lg border-b border-white/5 last:border-0"
              >
                {item}
              </Link>
            ))}
          </div>
        </div>
      </details>
    </div>
  );
};

const Navbar = () => {
  return (
    <nav className="fixed w-full z-50 bg-gray-900/80 backdrop-blur-md border-b border-white/10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          <Logo />

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-1">
            {navigationItems.map(item => (
              <NavLink key={item} item={item} />
            ))}
            <Link
              href="/join-us"
              className="ml-4 px-4 py-2 bg-white/10 text-white rounded-lg font-medium 
                hover:bg-white/20 transition-all duration-300"
            >
              Join Now
            </Link>
          </div>

          {/* Mobile Menu */}
          <MobileMenu />
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
